import { React, useState } from 'react';
import Input from '../input/input';

export default function Form({
	show,
	showModal,
	removeModal,
	handleSubmit,
	setLibrary,
	user,
	changedLibrary,
	setChangedLibrary,
}) {
	const [input, setInput] = useState({
		title: '',
		author: '',
		pages: '',
		completed: false,
	});

	const emptyInput = {
		title: '',
		author: '',
		pages: '',
		completed: false,
	};

	const handleChange = (event) => {
		setInput({
			...input,
			[event.target.id]: event.target.value,
		});
	};

	function addBook(event) {
		event.preventDefault();
		const newBook = {
			userid: user.userid,
			title: input.title,
			author: input.author,
			pages: input.pages,
			completed: input.completed,
			email: user.email,
		};
		fetch('https://damp-depths-04548.herokuapp.com/library-item', {
			method: 'POST',
			headers: {
				'Access-Control-Allow-Origin': '*',
				'Content-Type': 'application/json',
			},
			body: JSON.stringify({ ...newBook }),
		})
			.then((data) => data.json())
			.then((book) => console.log(book))
			.then(setChangedLibrary((prevStatus) => !prevStatus));
		handleSubmit(event);
		setInput(emptyInput);
	}

	return (
		<div
			className={`modal ${show} fade `}
			id='exampleModal'
			tabIndex='1'
			role='dialog'
			aria-labelledby='exampleModalLabel'
			aria-hidden='true'
			style={showModal}>
			<div className='modal-dialog' role='document'>
				<div className='modal-content text-center'>
					<div className='modal-header text-center'>
						<h5
							className='modal-title text-uppercase text-center'
							id='exampleModalLabel'>
							Add Book To Library
						</h5>
						<button
							type='button'
							className='btn-close'
							data-dismiss='modal'
							aria-label='Close'
							onClick={() => {
								removeModal();
								setInput(emptyInput);
							}}></button>
					</div>
					<div className='modal-body text-center'>
						<form>
							<Input
								type='text'
								id='title'
								name='title'
								label='Title'
								placeholder='Title'
								labelFor='title'
								setValue={input.title}
								handleChange={handleChange}
							/>
							<Input
								type='text'
								id='author'
								name='author'
								label='Author'
								placeholder='Author'
								labelFor='author'
								setValue={input.author}
								handleChange={handleChange}
							/>
							<Input
								type='number'
								id='pages'
								name='pages'
								label='Pages'
								placeholder='Pages'
								labelFor='pages'
								setValue={input.pages}
								handleChange={handleChange}
							/>
							<div className='form-group'>
								<label htmlFor='finished'>
									Have you finished the book?
								</label>
								<select
									onChange={handleChange}
									className='form-select form-select-lg mb-3'
									id='finished'
									aria-label='.form-select-lg form'>
									<option value={true}>Yes</option>
									<option value={false}>No</option>
								</select>
							</div>
							<button
								onClick={(e) => addBook(e)}
								className='btn rounded-pill btn-primary'>
								Submit
							</button>
						</form>
					</div>
				</div>
			</div>
		</div>
	);
}
